import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { TableListService } from '@app/core/http/table-list.service';
import {  QuestionPractise } from '@app/shared/models/question';
import { PractiseQuestionsList } from '@app/shared/models/question-list';
import { TableFilter } from '@app/shared/models/table-filter';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-question-list',
  templateUrl: './question-list.component.html',
  styleUrls: ['./question-list.component.css']
})
export class QuestionListComponent implements OnInit {
  private currentModule:any={};

  public questionList:QuestionPractise[];
  public tableFilter=<TableFilter>{};

  private subscription:Subscription;

  public isLoading:boolean=true;
  public isAsc: boolean = true;

  constructor(private _activeRoute:ActivatedRoute,private _tableService:TableListService) { }

  ngOnInit() {
    this.isLoading=true;
    this.currentModule=this._activeRoute.snapshot.params;

    this._activeRoute.queryParams.subscribe(
      (params) => {
        if (params.page) {
          this.tableFilter.page = params.page;
          this.tableFilter.limit = params.limit;
        } else if (params.search) {
          this.tableFilter.search = params.search;
        }else{
          this.tableFilter.page = 1;
          this.tableFilter.limit = 10;
          this.tableFilter.sortDirection = 'asc';
          this.tableFilter.sortColumn = 'name';
          this.tableFilter.search='';
        }
        this.getQuestionList();
      }
    )


  }

  getQuestionList(){
    this.subscription=this._tableService.getQuestionList(this.tableFilter,this.currentModule)
    .subscribe((res:PractiseQuestionsList)=>{
      this.questionList=res.questions;
      this.tableFilter=res.filter;
      this.isLoading=false;
    })
  }


  fitter(sortColumn) {
    this.isAsc = !this.isAsc ? this.isAsc = true : this.isAsc = false;
    this.tableFilter.sortDirection = this.isAsc ? 'asc' : 'desc';
    this.tableFilter.sortColumn = sortColumn;
    this.getQuestionList();
  }


  ngOnDestroy(): void {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    if(this.subscription) this.subscription.unsubscribe();
  }
}
