import { NgModule } from "@angular/core";
import { Routes, RouterModule, Route } from "@angular/router";
import { QuestionResolver } from "@app/modules/questions/reslovers/question.reslover";
import { QuestionBoardComponent } from "@app/modules/questions/question-board/question-board.component";
import { QuestionListComponent } from "@app/modules/questions/question-list/question-list.component";
import { QuestionComponent } from "@app/modules/questions/question/question.component";




const routes: Routes = [
  {
    path: ":module-name",
    component: QuestionComponent,
    children: [
      {
        path: "",
        component: QuestionBoardComponent,
        resolve: {
          questionBank: QuestionResolver
        }
      },
      {
        path: ":moduleName/:questionType",
        component: QuestionListComponent,
      }
    ],

  },

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
  providers: [
    QuestionResolver
  ]
})

export class QuestionRoutingModule { }
