import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CheckProgress } from '@app/shared/models/check-progress';
import { QuestionBank } from '@app/shared/models/question-bank';

@Component({
  selector: 'app-question-board',
  templateUrl: './question-board.component.html',
  styleUrls: ['./question-board.component.css']
})
export class QuestionBoardComponent implements OnInit {
  public questionbank: CheckProgress;

  constructor(private activeRoute: ActivatedRoute, private router: Router) {
    this.router.routeReuseStrategy.shouldReuseRoute = () => false;
  }


  ngOnInit() {
    this.questionbank = this.activeRoute.snapshot.data["questionBank"];
  }

  goToTablelist(data) {
    // if (this.questionbank.moduleType == 'READING')
    //   this.router.navigateByUrl(`AI-score/practise/reading/${data.questionType}/reading-practise`);
    // else
      this.router.navigate([data.moduleType, data.questionType], { relativeTo: this.activeRoute });
  }
}
